.skills {
  margin-top: 5%;
  min-height: 60vh;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  padding: 3rem;
}

.skill_box {
  left:50%;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}


.sub_box {
  width: 90%;
  text-align: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2.5rem;
}

.sub_box .fa {
  color: var(--title-color);
  margin-bottom: 1rem;
  font-size: 1.7rem;
}

.third {
  padding-top: 2rem;
}

.material-symbols-outlined {
  color: var(--title-color);
  margin-bottom: 1rem;
  font-size: 2.2rem;
  color: lightgray;
}

.skill_list,
.upcoming {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 1rem;
}

.skillwrap {
  width: 70px;
}

.skill_icon {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  align-items: center;
  padding-top: 0.7rem;
  box-shadow: 1.5px 1.5px 0px 0px #dadce0;
  margin: auto;
  justify-content: center;
}

.sk {
  font-size: var(--smaller-font-size);
  margin-bottom: 1rem;
}

.upcoming .skill_icon,
.upcoming .sk {
  color: lightgrey;
}

@media screen and (max-width: 768px) {
  .skills {
    width: 100%;
  }
  .skill_box{
    grid-template-columns: repeat(1, 1fr);
  }
  .skill_title {
    font-size: var(--normal-font-size);
  }
  .sub_box {
    width: 100%;
  }
  .skill_list {
    flex-wrap: wrap;
  }
}

