.contact-body {
  background: linear-gradient(to top, #cdd3eabe , white 60%);
  height: 100vh;
}

.contact {
  margin-top: 10%;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  padding: 3rem;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.title {
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1);
}

.subtitle_icon {
  display: block;
  margin-bottom: var(--mb-1);
}

.subtitle_icon {
  font-size: var(--h2-font-size);
}

.contact_form {
  width:80%;
  margin-left: auto;
  margin-right: auto;
}


.contact_form_input{
 width: 100%;
 margin-bottom: 1.5rem;
 border: none;
 box-shadow: 0px 15px 10px -15px #8f9ac2f3;  
 text-align: center; 
}

input {
  width: 100%;
  background-color: #fffefe;
  font-family:Courier, monospace;
  color: rgb(92, 92, 92);
  line-height: 1.5rem;
}


.email,
.name {
  border-bottom: 1px solid #8f9ac2f3; 
}

input:focus,
textarea:focus {
  background-color: #cdd3ea4d;
  outline: none;
  box-shadow: 0 0 10px #8f9ac2f3;
}

input::placeholder,
textarea::placeholder {
  color: rgb(192, 190, 190);
  font-size: var(--small-font-size);
  font-weight: var(--font-medium);
  text-align: center;
}

textarea{
  width: 100%;
  font-family:Courier, monospace;
  background-image: linear-gradient(#cdd3ea4d 50%, #fffefe 50%);
  background-size: 100% 3rem;
  line-height: 1.5rem;
  resize: none;
  color: rgb(92, 92, 92);
}


.contact_btn{
  background-color: #8f9ac2f3;
  color: white;
  padding: 0.5rem;
  border-radius: 10%;
  width: 50px;
  height: 50px;
  align-items: center;
}
.plane {
  display: none;
}

.contact_btn:hover {
  background-color: transparent;
  color: var(--title-color);
}


.contact_btn:focus .plan {
  display: none;
}

.contact_btn:hover .btn_text{
  display: none;
}

.contact_btn:hover .plane{
  display: inline-block;
  animation: mover 1s infinite;
  transition: 5s;
  font-size: 2rem;
  color: #8f9ac2f3;
}

@keyframes mover {
  0% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
  100% { transform: translateY(0); }
}

@media screen and (max-width: 768px) {
  .contact-body {
    background: linear-gradient(to bottom, #cdd3eabe , white 60%);
    height: 100vh;
  }

  .contact {
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
    width: 100%;
  }

  .contact_form {
    width: 100%;
  }

  input::placeholder,
  textarea::placeholder {
    font-size: var(--smaller-font-size);
  }
  
}