 
.header {
  width:100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--container-color);
}

.nav {
  height: calc(var(--header-height) );
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}

.nav__logo, 
.nav__toggle {
  color: var(--title-color);
}

.nav__logo {
  font-weight:var(--font-bold);
}

.nav__logo:hover {
  color: #fafafa;
  text-shadow: 2px 2px 4px #717da8c1;
}

.nav__list{
  display: grid;
  grid-template-columns: repeat(4,1fr);
}

.nav__link {
  display:flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--text-color);
  font-weight: var(--font-regular);
  transition: .3s;
}

.nav__icon {
  display: none  !important;
  /* the inline style display:block is overriding */
}


.nav__close,
.nav__toggle {
  display: none;
}


.nav__link:hover {
  color: var(--title-color-dark);
}


/* ---------- RESPONSIVE SIZE ----------*/
/* ----- medium -----*/
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
  }

  .nav {
    height: var(--header-height);
  }

  .nav__menu{
    position: fixed;
    left: 0;
    width: 100%;
    bottom: 0;
    background-color: var(--body-color);
    padding: 1rem 1.5rem 3rem 1rem;
    box-shadow: 0 -1px 4px rgba(0,0,0,0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: .3;
  }

  .nav__list {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 2rem;
  }

  .nav__icon {
    font-size: var(--normal-font-size);
    display: block;
  }

  .nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1rem;
    cursor: pointer;
    color: var(--text-color);
  }

  .nav__close:hover {
    color: var(--title-color-dark);
  }


  .nav__toggle,
  .nav__close {
    display: block;
  }



  /* Hide Menu*/
  
  .hide-menu .nav__close {
    display: none;
  }

  .hide-menu .nav__item {
    display: none;
  }

  .hide-menu {
    bottom: -100%;
  }

  .nav__toggle{
    color:var(--text-color);
  }
  .nav__toggle:hover {
    cursor: pointer;
    color: var(--title-color-dark);
  }

  .nav_text {
    font-size: var(--smaller-font-size);
  }
  
  .toggle__icon {
    font-size: var(--smaller-font-size);
  }

  
}


/* ----- small -----*/
@media screen and (max-width: 350px) {
  .nav__menu {
    padding: 2rem 0.25rem 4rem;
  }
  

  .nav__list {
    column-gap: 0;
  }
}