

p {
  line-height: 1.5;
}

.hide {
  display: none;
}

i {
  color: var(--text-color);
}

i:hover {
  color: var(--title-color-dark);
}

.home__section {
  width: 100%;
}

.home__container{
  width: 100%;
  padding-top: 1.2rem;
}

.home__content {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
  height: 25rem;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 3rem;
}

.home__title {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.home__social {
  display: grid;
  grid-template-columns: repeat(2,1rem);
  column-gap: 1rem; 
}


.home__social-icon {
  font-size: var(--h2-font-size);
}

.home__img {
  width: 300px;
  height: 300px;
  background: radial-gradient(#fafafa, transparent 50%, #acb9e7c1);
  border-radius: 50%;
  transform: rotate(20deg);
  animation: LeftToRight 3s linear alternate-reverse infinite 1s;
}

.talkings {
  width:400px;
  margin-bottom: var(--mb-1);
}

.board {
  margin-top: 1rem;
  text-align: center;
  line-height: 40px;
}

.greeting{
  font-family: 'Caveat', cursive;
  color: #8f9ac2f3;
  font-size: var(--h2-font-size);
}



@keyframes LeftToRight {
  0% {
    transform: rotate(20deg);
  }
  
  25% {
    transform: rotate(10deg);
  }
  
  50% {
      transform: rotate(0deg);
  }
  
  75% {
    transform: rotate(-10deg);
  }
   
  100% {
    transform: rotate(20deg);
  }
}

.about__container {
  display: flex;
  width: 80%;
  justify-content: space-evenly;
  align-items: center;
}

.about__box {
  text-align: center;
}


.about__title {
  font-weight: var(--font-normal);
  font-size: var(--small-font-size);
}

.about__sticker {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.about__icon {
  font-size: var(--normal-font-size);
  margin-bottom: var(--mb-0-75);
  padding-top: 0.75rem;
  width: 40px;
  height: 40px;
  align-items: center;
  border-radius: 50%;
  box-shadow: 1.5px 1.5px 0px 0px #dadce0;
}


.about__icon:hover {
  cursor: pointer;
  box-shadow: 1.5px 1.5px 0px 0px #8e8f91;
  animation: LeftToRight linear alternate-reverse infinite 2s; 
}



.home__subtitle{
  padding-bottom: 1rem;
}

.board {
  box-shadow: 2px 2px 2px 2px #dadce0;
  height: 200px;
  width: 400px;
  color: var(--title-color);
  font-size: var(--small-font-size);
}


.board a {
  background-color: #8f9ac2f3;
  font-family: 'Caveat', cursive;
  color: white;
  font-size: var(--small-font-size);
  padding: 3px 5px 3px 2px;
}

.comment {
  font-size: var(--smaller-font-size);
  color: var(--text-color);
}

.line2 {
  height: 40px;
  border-bottom: 1px solid #acb9e7c1;
}
.line3 {
  height: 40px;
  border-bottom: 1px solid #acb9e7c1;
}
.line4 {
  height: 40px;
  border-bottom: 1px solid #acb9e7c1;
}
.line5 {
  height: 40px;
  border-bottom: 1px solid #acb9e7c1;
}


@media screen and (max-width: 768px) {
  .home__section {
    min-width: fit-content;
    padding-top: 0;
  }

  .home__container{
    padding-bottom: 10rem;
    padding-top: 0;
  }

  .home__content{
    margin-top: 1.5rem;
    align-items: start;
    flex-wrap: wrap;
  }

  .home__img {
    margin-left: auto;
    margin-right: auto;
    size: 70%;
  }

  .about__content {
    position: absolute;
    width: 100%;
    margin-top: 60%;
  }
  
  .about__container {
    width: 100%;
    margin-top: 15rem;
    margin-left: auto;
    margin-right: auto;
  }

  .board {
    width: 50%;
  }


}

@media screen and (max-width: 454px) {
  .home-body {
    width: 100%;
    min-width: fit-content;
    height: 100vh;
    box-sizing: border-box;
  }

  .home__section {
  }
  .home__container {
    padding-top: 0;
    width: 90%;
  }

  .home__content {
    margin-top: 10%;
    margin-bottom: 10%;
    height: 18rem;
    flex-wrap: wrap;
  }

  .about__content {
    position: absolute;
    width: 100%;
    margin-top: 25rem;
  }

  .home__title{
    justify-content: space-evenly;
  }
  .home__subtitle,
  .talkings {
    text-align: center;
  }

  .about__container {
    justify-content: space-around;
    margin-top: 18rem;
    margin-bottom: 10rem;
  }

  .home__data {
    margin-left:auto;
    margin-right: auto;
  }
  .home__social-icon{
    font-size: 1.7rem;
  }
  .board {
    font-size: var(--smaller-font-size);
    width: 70%;
  }
}
